/* 用户模块样式开始 */
 .switch, .switch.on {
	background: url(../../../public/resource/image/a023/bulb_control_icon_on_off_n.png) center no-repeat;
	background-size: calc(9.46rem * 0.5);
}
 .switch.off, .switch.disabled {
	background: url(../../../public/resource/image/a023/bulb_control_icon_on_off_pre.png) center no-repeat;
	background-size: calc(9.46rem * 0.5);
}
 .switch2, .switch2.on {
	background: url(../../../public/resource/image/a023/bulb_control_icon_on_off_n.png) center no-repeat;
	background-size: calc(9.46rem * 0.35);
}
 .switch2.off, .switch2.disabled {
	background: url(../../../public/resource/image/a023/bulb_control_icon_on_off_pre.png) center no-repeat;
	background-size: calc(9.46rem * 0.35);
}
 .lamp-dim{
	/** 预加载图片 **/
	background: url("../../../public/resource/image/a023/color_disk_dim_off.png") center no-repeat
}
 .lamp-dim.bg {
	background: url("../../../public/resource/image/a023/color_disk_dim_on.png") center no-repeat;
	background-size: 100% 100%;
}
 .lamp-dim.bg.disabled {
	background: url("../../../public/resource/image/a023/color_disk_dim_off.png") center no-repeat;
	background-size: 100% 100%;
}
 .lamp-rgbw{
	/** 预加载图片 **/
	background: url("../../../public/resource/image/a023/color_disk_rgb_off.png") center no-repeat
}
 .lamp-rgbw.bg {
	background: url("../../../public/resource/image/a023/color_disk_rgb_on.png") center no-repeat;
	background-size: 100% 100%;
}
 .lamp-rgbw.bg.disabled {
	background: url("../../../public/resource/image/a023/color_disk_rgb_off.png") center no-repeat;
	background-size: 100% 100%;
}
 .lamp-cct{
	/** 预加载图片 **/
	background: url("../../../public/resource/image/a023/color_temperature_off.png") center no-repeat
}
 .lamp-cct.bg {
	background: url("../../../public/resource/image/a023/color_temperature_on.png") center no-repeat;
	background-size: 100% 100%;
}
 .lamp-cct.bg.disabled {
	background: url("../../../public/resource/image/a023/color_temperature_off.png") center no-repeat;
	background-size: 100% 100%;
}
 .slider-brightness .weak{
		background: url("../../../public/resource/image/a023/device_icon_brightness_weak.png");
		background-size: 100% 100%;
	}
 .slider-brightness .strong{
		background: url("../../../public/resource/image/a023/device_icon_brightness_strong.png");
		background-size: 100% 100%;
	}
 .slider-brightness .tip-wrap{
		position: absolute;
		top: 0;
		left: calc((2rem + .58rem));
		width: calc(100% - (2rem + .58rem) * 2);
		margin: 0 auto;
	}
 .slider-brightness .tip{
		text-align: center;
		font-size: 13px;
		font-weight: 700;
		background: url("../../../public/resource/image/a023/light_control_air_bubbles.png");
		background-size: 100% 100%;
	}
 .slider-brightness.close .weak {
			/*background: url("$img-dir/equipment_icon_low_disable.png");*/
			/*background-size: 100% 100%;*/
			opacity:0.3;
}
 .slider-brightness.close .strong {
			/*background: url("$img-dir/equipment_icon_high_disable.png");*/
			/*background-size: 100% 100%;*/
			opacity:0.3;
}
 .slider-brightness.close .rc-slider {
			opacity:0.3;
}




