

.modal{
	position:fixed;
	top: 0;
	left: 0;
	z-index: 1011;
	width: 100%;
	height: 100%;
}
	.modal .backdrop{
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.6);
	}
	.modal .dialog{
		width: calc(45rem * 0.5);
		position: absolute; 
		top: 50%; 
		left: 50%; 
		-webkit-transform: translate(-50%, -50%); 
		    -ms-transform: translate(-50%, -50%); 
		        transform: translate(-50%, -50%);
		background: rgba(252, 252, 252, 0.8);
		border-radius: 12px;
		text-align: center;
	}
	.modal .dialog .title{
			font-size: 17px;
			color: #333333;
			margin: calc(2.67rem * 0.5);
		}
	.modal .dialog .content{
			font-size: 13px;
			color: #333333;
			line-height: 1.44rem;
			margin: calc(2.67rem * 0.5);
			word-break: break-word;
		}
	.modal .dialog .progress{
			font-size: 13px;
			color: #333333;
			line-height: 1.44rem;
			margin: calc(2.67rem * 0.5);
			word-break: break-word;
		}
	.modal .dialog .progress div{
				margin-bottom: 1rem;
			}
	.modal .dialog .progress a{
				color: #F59C24;
				margin-left: 0.5rem;
			}
	.modal .dialog .operate{
			height: calc(7.25rem * 0.5);
			line-height: calc(7.25rem * 0.5);
			border-top: solid calc(0.08rem  * 0.5) rgba(77,77,77,0.78);
		}
	.modal .dialog .operate a{
				display: inline-block;
				-webkit-box-sizing: border-box;
				        box-sizing: border-box;
				font-size: 17px;
				border-left: solid calc(0.08rem  * 0.5) rgba(77,77,77,0.78)
			}
	.modal .dialog .operate a:first-child{
	border-left: none;
}
	.modal .dialog .operate a, .modal .dialog .operate a:focus, .modal .dialog .operate a:active, .modal .dialog .operate a:hover{
	color: #F59C24;
}


