/* 用户模块样式开始 */
 .layout{
	width: 100vw;
	height: 100vh;
}
 /** 多层布局样式 begin **/
 .left .multilayer-enter {
		-webkit-transform: translate3d(100%, 0, 0);
		        transform: translate3d(100%, 0, 0)
	}
 .left .multilayer-enter.multilayer-enter-active {
	-webkit-transition: -webkit-transform .3s ease;
	transition: -webkit-transform .3s ease;
	-o-transition: transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
	-webkit-transform: translate3d(0, 0, 0);
	        transform: translate3d(0, 0, 0);
	opacity: 1;
}
 .left .multilayer-leave {
			-webkit-transform: translate3d(0, 0, 0);
			        transform: translate3d(0, 0, 0)
	}
 .left .multilayer-leave.multilayer-leave-active {
	-webkit-transition: -webkit-transform .3s ease;
	transition: -webkit-transform .3s ease;
	-o-transition: transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
	-webkit-transform: translate3d(-100%, 0, 0);
	        transform: translate3d(-100%, 0, 0);
}
 .right .multilayer-enter {
		display: block;
		-webkit-transform: translate3d(-100%, 0, 0);
		        transform: translate3d(-100%, 0, 0)
	}
 .right .multilayer-enter.multilayer-enter-active {
	-webkit-transition: -webkit-transform .3s ease;
	transition: -webkit-transform .3s ease;
	-o-transition: transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
	-webkit-transform: translate3d(0, 0, 0);
	        transform: translate3d(0, 0, 0);
}
 .right .multilayer-leave {
			-webkit-transform: translateX(0);
			    -ms-transform: translateX(0);
			        transform: translateX(0)
	}
 .right .multilayer-leave.multilayer-leave-active {
	-webkit-transition: -webkit-transform .3s ease;
	transition: -webkit-transform .3s ease;
	-o-transition: transform .3s ease;
	transition: transform .3s ease;
	transition: transform .3s ease, -webkit-transform .3s ease;
	-webkit-transform: translate3d(100%, 0, 0);
	        transform: translate3d(100%, 0, 0);
}
 /** 多层布局样式 end **/
