

.modal{
	position:fixed;
	top: 0;
	left: 0;
	z-index: 1011;
	width: 100%;
	height: 100%;
}
	.modal .backdrop{
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.6);
	}
	.toast{
	width: calc(48.92rem * 0.5);
	position: absolute; 
	z-index: 1012;
	top: 50%; 
	left: 50%; 
	-webkit-transform: translate(-50%, -50%); 
	    -ms-transform: translate(-50%, -50%); 
	        transform: translate(-50%, -50%);
	background: rgba(0, 0, 0, 0.8);
	border-radius: 8px;
	text-align: center;
}
	.toast .content{
		font-size: 13px;
		line-height: 1.44rem;
		margin: calc(2.67rem * 0.5);
		word-break: break-word;
	}


