.card {
  position: relative;
  display: inline-block;
  width: 10.4rem;
  height: 10.25rem;
  padding: 1.665rem 1.35rem 1.085rem 1.25rem;
  background-color: #ffffff;
  border-radius: 0.665rem;
  color: #333333;
  text-align: left
}
.card.disabled {
  background: rgba(255,255,255,0.3);
}
.card-row.fx {
  display: -ms-flexbox;
  display: flex;
}
.card-row.name-multi {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  margin-top: 1.665rem;
  font-size: 18px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: normal;
  word-break: break-word;
  height: 40px;
}
.card-row.room {
  font-size: 15px;
  line-height: 2rem;
}
.card-row.room .room-name {
        -ms-flex: 1 1;
            flex: 1 1;
        overflow: hidden;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
        white-space: nowrap;
}
.card-row .devices-status {
      -ms-flex: 1 1;
          flex: 1 1;
      text-align: right;
}
.card-row .status {
      display: inline-block;
      margin-bottom: 0.585rem;
}


