

html, body{
  height: 100%;
  min-height: 100%;
  font-family: "PingFang SC", "DroidSans", "Arial", "Heiti SC", "Microsoft YaHei", "sans-serif";
  font-size: 10px;
  -webkit-user-select: none;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: transparent;
}

body {
  margin: 0;
  padding: 0;
  color: #ffffff;
	background: #3A4056;
}

/* 重置列表元素 */

ul, ol { list-style: none; }

/* 重置文本格式元素 */

a, a:hover, a:active, a:focus{
  text-decoration: none;
  color: #333333;
}

/* 重置输入框 */

input{ outline:none; }

/* 重置文本块 */

h1, h2, h3, h4, h5, h6, p { 
	margin: 0;
	padding: 0;
}

